import React from 'react'
// import"bootstrap/dist/css/bootstrap.css";
import { NavLink } from 'react-router-dom'
// adding images
import logo from "../images/rocket.svg"


const Navbar = () => {
  return (
    <>
    <div className="container-fluid nav_bg">
      <div className="row">
        <div className="col-10 mx-auto">
        <nav className="navbar navbar-expand-lg navbar-light ">
          <div className="container-fluid">
            <NavLink className="navbar-brand" to="/"><span className="partial_logo">Code </span> <img className="main_logo" src={logo} alt="logo"/></NavLink>
       <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
       </button>
     <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <NavLink className="nav-link active" aria-current="page" to="/">Home</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link" to="/about">About</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link" to="/skill">Skills</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link" to="/project">Project</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link" to="/contact">Contact</NavLink>
        </li>
      </ul>
    </div>
  </div>
</nav>
        </div>
      </div>
    </div>
     
    </>
  )
}

export default Navbar;
